import { parseTime } from "@/utils";
import SalesCard from "@/components/SalesCard";
export default {
  props: {
    city: String,
    date: Date,
    isShop: { type: Boolean, default: false },
  },
  components: { SalesCard },
  data() {
    return {
      active: 1,
    };
  },
  computed: {
    tabs() {
      const type = this.isShop ? "shop" : "operate";
      return [
        {
          icon: require(`assets/${type}/today.png`),
          label: "本日统计",
        },
        {
          icon: require(`assets/${type}/month.png`),
          label: "本月统计",
        },
        {
          icon: require(`assets/${type}/year.png`),
          label: "本年统计",
        },
      ];
    },
    bgStyle() {
      return {
        backgroundImage: `url(${require(`assets/${
          this.isShop ? "shop" : "operate"
        }/city-bg.png`)})`,
      };
    },
  },
  methods: {
    parseTime,
  },
};
